// src/app/page.js
// Homepage
import Navigation from '../components/Navigation';
import Hero from '../components/Hero';
import ServiceCard from '../components/ServiceCard';
import Footer from '../components/Footer';

export default function Home() {
  return (
    <main>
      <Navigation />
      <Hero />
      
      {/* Leistungen Abschnitt */}
      <section className="py-16 bg-gray-900">
        <div className="container mx-auto px-6">
          <h2 className="text-3xl font-bold text-white text-center mb-12">
            <span className="border-b-2 border-bf-gold pb-2">UNSERE LEISTUNGEN</span>
          </h2>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <ServiceCard 
              title="Pflegekraftvermittlung" 
              description="Individuelle Betreuung nach Ihren Bedürfnissen" 
            />
            <ServiceCard 
              title="Qualitätssicherung" 
              description="Regelmäßige Kontrollen und Feedback-Gespräche" 
            />
            <ServiceCard 
              title="Rundumbetreuung" 
              description="Administrative und rechtliche Unterstützung" 
            />
          </div>
          
          <div className="text-center mt-12">
            <a 
              href="/leistungen" 
              className="text-bf-gold hover:underline"
            >
              Mehr zu unseren Leistungen
            </a>
          </div>
        </div>
      </section>
      
      {/* Über Uns Kurz-Abschnitt */}
      <section className="py-16 bg-bf-black">
        <div className="container mx-auto px-6">
          <div className="flex flex-col md:flex-row items-center">
            <div className="md:w-1/2 mb-8 md:mb-0">
              <h2 className="text-3xl font-bold text-white mb-6">
                <span className="border-b-2 border-bf-gold pb-2">ÜBER UNS</span>
              </h2>
              <p className="text-white mb-6">
                Black Forge Consulting ist Ihr zuverlässiger Partner für die Vermittlung 
                qualifizierter Pflegekräfte. Wir verstehen die individuellen Bedürfnisse 
                unserer Kunden und bieten maßgeschneiderte Lösungen für optimale Betreuung.
              </p>
              <a 
                href="/ueber-uns" 
                className="text-bf-gold hover:underline"
              >
                Mehr über uns erfahren
              </a>
            </div>
            <div className="md:w-1/2 md:pl-10">
              <div className="bg-gray-700 h-64 w-full flex items-center justify-center">
                <p className="text-center text-gray-400">Bild: Unser Team</p>
              </div>
            </div>
          </div>
        </div>
      </section>
      
      {/* Kontakt CTA */}
      <section className="py-12 bg-gray-900">
        <div className="container mx-auto px-6 text-center">
          <h2 className="text-2xl font-bold text-white mb-6">
            Bereit für professionelle Pflege?
          </h2>
          <a 
            href="/kontakt" 
            className="bg-bf-gold text-bf-black px-8 py-3 text-lg font-semibold rounded hover:bg-yellow-600 transition-colors"
          >
            KONTAKT AUFNEHMEN
          </a>
        </div>
      </section>
      
      <Footer />
    </main>
  );
}