// ServiceCard.js
// Wiederverwendbare Karte für einzelne Dienstleistungen
export default function ServiceCard({ title, description }) {
    return (
      <div className="bg-bf-black p-6 rounded-lg shadow-md border border-gray-800 hover:border-bf-gold transition-colors">
        <h3 className="text-xl font-semibold text-bf-gold mb-3">{title}</h3>
        <p className="text-white">{description}</p>
      </div>
    );
  }